<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid text-start">
        <p class="fs-5 m-2">
          {{ this.$route.meta.title }}
        </p>
        <div v-if="!categoryOneslist">
          <i class="fas fa-spinner fa-spin fs-1 mt-3"></i>
        </div>
        <div v-else class="row">
          <div
            v-for="item in categoryOneslist"
            :key="item.id"
            class="col-lg-3 col-md-3 col-sm-4 col-6"
          >
            <router-link
              :to="{ name: 'CategoryOne', params: { categoryOneId: item.id } }"
              class="d-block text-decoration-none"
            >
              <img
                :src="API_STORAGE_URL + item.image_path"
                class="img-fluid rounded-top"
              />
              <span
                class="btn btn-theme d-block fs-5 my-2 rounded-0 rounded-bottom"
              >
                {{ item.name }}
              </span>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TheSidemenu from "../../components/TheSidemenu";
import TheTopmenu from "../../components/TheTopmenu";
import ArefApi from "../../libs/api.js";
import utils from "../../libs/utils";

export default {
  name: "Shop",
  created() {
    this.arefApi = new ArefApi(this);
    this.utils = new utils();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      categoryOneslist: null,
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
    };
  },
  methods: {
    getUserCategoryOne() {
      this.arefApi
        .getUserCategoryonesIndex(1, true)
        .then((resp) => {
          this.categoryOneslist = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
  },
  mounted() {
    this.getUserCategoryOne();
  },
};
</script>

